import React, { useState } from "react";
import { Container } from "react-bootstrap";
import { Authorization, DataTableController, DataTableFilter, DataTableViewer } from "../components";
import { useStateContext } from "../contexts/ContextProvider";

export default function SaleOrdersPage() {
  const { dataState } = useStateContext();
  const tableName = "sales";
  const subTableName = "saledetails";
  return (
    <Authorization>
      <Container>
        {(dataState && dataState[tableName] && (
          <DataTableViewer
            id="purchases"
            title=""
            subTitle=""
            detail
            tableName={tableName}
            subTableName={subTableName}
            subQueryKey="saleInvoice"
          >
            <DataTableFilter>
              <DataTableFilter.Time label="" column="saleDateTime" type="date" />
              <DataTableFilter.Number label="" column="total" min max />
            </DataTableFilter>
          </DataTableViewer>
        )) ||
          "...."}
      </Container>
    </Authorization>
  );
}
