import React from "react";
import { Container } from "react-bootstrap";
import { Authorization, DataTableController, DataTableFilter } from "../components";
import { useStateContext } from "../contexts/ContextProvider";

export default function StockStatusPage() {
  const { dataState } = useStateContext();
  const tableName = "genericgoods";
  return (
    <Authorization>
      <Container>
        {(dataState && dataState[tableName] && (
          <DataTableController id="stockstatus" title="" tableName={tableName}>
            <DataTableFilter>
              <DataTableFilter.Compare label="" compare="l" columns={["inventory", "sale_Inventory"]} />
              <DataTableFilter.Compare label="" compare="g" columns={["inventory", "sale_Inventory"]} />
            </DataTableFilter>
          </DataTableController>
        )) ||
          "...."}
      </Container>
    </Authorization>
  );
}
