import React from "react";
import { Container } from "react-bootstrap";
import { Authorization, DataTableController, DataTableFilter } from "../components";
import { useStateContext } from "../contexts/ContextProvider";

const SchedulesPage = () => {
  const { dataState } = useStateContext();
  const tableName = "schedules";
  const type = {
    keys: ["scheduleNo", "eAccount", "workDate", "start", "leave"],
    settings: [
      { type: "disable" },
      { type: "foreignKey", ref: "employees", column: "account", cname: "name" },
      { type: "date" },
      { type: "time" },
      { type: "time" },
    ],
  };
  return (
    <Authorization>
      <Container>
        {(dataState && dataState[tableName] && (
          <DataTableController id="schedules" title="" tableName={tableName} type={type} edit>
            <DataTableFilter>
              <DataTableFilter.Time label="" type="date" column="workDate" />
              <DataTableFilter.Time label="" type="time" column="start" />
              <DataTableFilter.Time label="" type="time" column="leave" />
              <DataTableFilter.Select
                label=""
                refTable="employees"
                column="eAccount"
                refColumn="account"
                cname="name"
              />
            </DataTableFilter>
          </DataTableController>
        )) ||
          "...."}
      </Container>
    </Authorization>
  );
};

export default SchedulesPage;
