import React from "react";
import { Container } from "react-bootstrap";
import { Authorization, DataTableController, DataTableFilter } from "../components";
import { useStateContext } from "../contexts/ContextProvider";

const SOPRecordsPage = () => {
  const { dataState } = useStateContext();
  const tableName = "soprecords";
  const type = {
    keys: ["registerDateTime", "sopClass", "eAccount"],
    settings: [
      { type: "datetime-local" },
      {
        type: "foreignKey",
        ref: "sopform",
        column: "sopClass",
        cname: "sopClass",
      },
      {
        type: "foreignKey",
        ref: "employees",
        column: "account",
        cname: "name",
      },
    ],
  };
  return (
    <Authorization>
      <Container>
        {(dataState && dataState[tableName] && (
          <DataTableController id="soprecords" title="" tableName={tableName} type={type}>
            <DataTableFilter>
              <DataTableFilter.Time label="" type="datetime-local" column="registerDateTime" />
            </DataTableFilter>
          </DataTableController>
        )) ||
          "...."}
      </Container>
    </Authorization>
  );
};
export default SOPRecordsPage;
