import React from "react";
import { Container } from "react-bootstrap";
import { Authorization, DataTableController, DataTableFilter } from "../components";
import { useStateContext } from "../contexts/ContextProvider";

const PunchRecordsPage = () => {
  const { dataState } = useStateContext();
  const tableName = "punchrecords";
  const type = {
    keys: ["punchDateTime", "eAccount", "status", "GPS"],
    settings: [
      { type: "datetime-local" },
      { type: "foreignKey", ref: "employees", column: "account", cname: "name" },
      { type: "enum", values: ["", ""] },
      { type: "img" },
    ],
  };
  return (
    <Authorization>
      <Container>
        {(dataState && dataState[tableName] && (
          <DataTableController id="punchrecords" title="" tableName={tableName} type={type} edit>
            <DataTableFilter>
              <DataTableFilter.Time label="" type="datetime-local" column="punchDateTime" />
              <DataTableFilter.Select
                label=""
                refTable="employees"
                column="eAccount"
                refColumn="account"
                cname="name"
              />
            </DataTableFilter>
          </DataTableController>
        )) ||
          "...."}
      </Container>
    </Authorization>
  );
};

export default PunchRecordsPage;
