import React, { useState } from "react";
import { Container } from "react-bootstrap";
import { Authorization, DataTableController, DataTableFilter, DataTableViewer } from "../components";
import { useStateContext } from "../contexts/ContextProvider";

export default function PurchaseOrderPage() {
  const { dataState } = useStateContext();
  const tableName = "purchases";
  const subTableName = "purchasedetails";
  const type = {
    keys: ["eAccount"],
    settings: [
      {
        type: "foreignKey",
        ref: "employees",
        column: "account",
        cname: "name",
      },
    ],
  };
  return (
    <Authorization>
      <Container>
        {(dataState && dataState[tableName] && (
          <DataTableViewer
            id="purchases"
            title=""
            subTitle=""
            detail
            tableName={tableName}
            subTableName={subTableName}
            type={type}
            subQueryKey="purchaseInvoice"
          >
            <DataTableFilter>
              <DataTableFilter.Time label="" column="purchaseDateTime" type="datetime-local" />
              <DataTableFilter.Number label="" column="total" min max />
              <DataTableFilter.Select
                label=""
                column="eAccount"
                refTable="employees"
                refColumn="account"
                cname="name"
              />
            </DataTableFilter>
          </DataTableViewer>
        )) ||
          "...."}
      </Container>
    </Authorization>
  );
}
